// Vuex            .       
// Store         Store  
import { createStore } from "vuex";
//       
//vuex state     
import createPersistedState from "vuex-persistedstate";

const store = createStore({
  state() {
    //    . State   Component computed  .   : this.$store.state.items
    return {
      user: null,
      userId: '',
      userPw: '',
      userNo: '',
      isLogin: false,
      isLoginError: false
    };
  },
  mutations: {
    //Mutation  State . . Mutations   Component Methods  .   : this.$store.commit('/')
    
    //        
    localUser(state, payload) {
      state.userId = payload.userId;
      state.userPw = payload.userPw;
      state.userNo = payload.userNo;
    },
    user(state, data) {
      state.user = data;
    },
    loginSuccess(state) {
      state.isLogin = true
      state.isLoginError = false
    },
    loginError(state) {
      state.isLogin = false
      state.isLoginError = true
    },
  },
  plugins: [
    createPersistedState()
  ]
});

export default store;